.timer {
  width: 500px;
  justify-content: space-evenly;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.field {
  width: 100px;
}

.clock-face {
  height: 500px;
  width: 500px;
  background-color: #0ff;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.date-time-data {
  height: 50px;
  width: 200px;
  color: #8a2be2;
  text-align: center;
  font-size: 20px;
  position: absolute;
  top: 270px;
  left: 150px;
}

.alert-data {
  height: 50px;
  width: 200px;
  color: red;
  text-align: center;
  font-size: 20px;
  position: absolute;
  top: 320px;
  left: 150px;
}

.arrow {
  width: 10px;
  transform-origin: bottom;
  border-radius: 5px;
  position: absolute;
  left: 245px;
}

.sec-arrow {
  height: 250px;
  background-color: green;
  top: 0;
}

.min-arrow {
  height: 200px;
  background-color: red;
  top: 50px;
}

.hour-arrow {
  height: 150px;
  background-color: #00f;
  top: 100px;
}

.arrow-axis-cover {
  height: 20px;
  width: 20px;
  background-color: green;
  border-radius: 50%;
  position: absolute;
  top: 240px;
  left: 240px;
}

.clock-digit-container {
  height: 250px;
  width: 100px;
  transform-origin: bottom;
  position: absolute;
  top: 0;
  left: 200px;
}

.clock-digit {
  width: 100px;
  height: 100px;
  color: #00f;
  justify-content: center;
  align-items: center;
  font-size: 54px;
  display: flex;
}

.clock-marking-container {
  height: 250px;
  width: 10px;
  transform-origin: bottom;
  position: absolute;
  top: 0;
  left: 245px;
}

.clock-marking {
  height: 20px;
  width: 10px;
  background-color: #00f;
  border-radius: 5px;
  font-size: 54px;
}

